<template>
  <div class="textTerm">
    <span style="min-width: 100px;display: inline-block;text-align: right;padding-right: 10px;">{{titName}}：</span>
    <Select v-model.trim="UnitName" size="large" :placeholder="'请输入' + titName" @on-change="srldChange(UnitName)" style="width: 200px;">
      <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>
  </div>
</template>

<script>
export default {
  name: 'TextTerm',
  data () {
    return {
      /* input内容 */
      UnitName: '',
      typeList: [
        {
          value: '',
          label: '请选择'
        },
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ]
    }
  },
  props: {
    /* 字段标题 */
    titName: {},
    /* 字段key值 */
    titKey: {}
  },
  mounted () {

  },
  methods: {
    srldChange () {
      if (this.UnitName === null || this.UnitName === '' || this.UnitName === undefined) {
        this.$emit('delQueryTrem', this.titKey)
      } else {
        let obj = {
          name: this.titName,
          text: this.UnitName,
          key: this.titKey,
          value: this.UnitName
        }
        this.$emit('updataQueryData', obj)
      }
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .cascadeTerm{

  }
</style>
